/* Post Block
===================================*/
.gardenar-post-card {
	.article-inner-wrapper {
		@include transition_default();
		.entry-content {
			margin-top: 15px;
		}
	}
	//Post Title
	.entry-title {
		font-size: 24px;
		line-height: 30px;
		margin-bottom: 12px;
		font-weight: 600;
		a {
			color: $title_color;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			&:hover {
				@extend .primaryColor;
			}
		}
	}
	.entry-footer {
		margin-top: 22px;
	}
	&:hover {
		.article-inner-wrapper {
			.post-thumbnail {
				img {
					transform: scale(1.1);
				}
			}
		}
	}
}

//Post Thumbnail
.post-thumbnail-wrap {
	position: relative;
	.post-thumbnail {
		overflow: hidden;
		margin-bottom: 0;
		line-height: 0;
		img {
			object-fit: cover;
			object-position: center;
			transition: all .5s;
			transform-origin: bottom left;
		}
	}
	.rt-video {
		position: absolute;
		top: 50%;
		left: 0;
		right: 0;
		transform: translateY(-50%);
		text-align: center;
		display: flex;
		justify-content: center;
		z-index: 1;
		.video-popup-icon {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 50px;
			height: 50px;
			border-radius: 50%;
			background-color: $white_color;
			color: $primary_color;
			line-height: 42px;
			text-align: center;
			font-size: 18px;
		}
	}
	.swiper-slide {
		line-height: 0;
	}
}

//Blog default
.blog-default {
	.entry-wrapper {
		padding: 30px 40px 40px;
	}
	.article-inner-wrapper {
		position: relative;
		overflow: hidden;
		border-radius: 15px;
		border: 1px solid #ECEBE4;
		background-color: $body_bg_color;
	}
}
.blog-list {
	  .entry-wrapper {
		  padding: 24px 0 0 0;
		  .entry-title {
			  font-size: 30px;
			  font-weight: 600;
		  }
	  }
	.separate-meta {
		a {
			background-color: $body_bg_color;
			color: $primary_color;
			&:hover {
				background-color: $tertiary_color;
				color: $button_text_color;
			}
		}
	}
	.article-inner-wrapper {
		border-radius: 0;
		border: 0;
		background-color: transparent;
		margin-bottom: 16px;
		.post-thumbnail-wrap {

			.post-thumbnail {
				position: relative;
				overflow: hidden;
				border-radius: 15px;
				.post-thumb-link {
					border-radius: 15px;
					width: 100%;
					img {
						border-radius: 15px;
						width: 100%;
					}
				}
			}
		}
	}
}

.blog-list-2 {
	.separate-meta {
		a {
			background-color: $gray20;
			color: $primary_color;
			&:hover {
				background-color: $primary_color;
				color: $white_color;
			}
		}
	}
	.entry-wrapper {
		flex: 1;
	}
	.article-inner-wrapper {
		@media(min-width: 1025px) {
			display: flex;
			align-items: center;
			gap: 30px;
		}
		position: relative;
		overflow: hidden;
	}
	.post-thumbnail-wrap {
		@media(min-width: 1025px) {
			max-width: 410px;
		}
		.post-thumbnail {
			border-radius: 20px;
			@media(max-width: 1024px) {
				margin-bottom: 30px !important;
				img {
					width: 100%;
				}
			}
		}
	}
}

.blog-grid-2 {
	.post-thumbnail {
		border-radius: 15px;
	}
	.post-thumbnail-wrap {
		margin-bottom: 20px;
		img {
			border-radius: 15px;
		}

		.posted-on {
			position: absolute;
			left: 12px;
			bottom: 12px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			background-color: $primary_color;
			border-radius: 15px;
			color: $white_color;
			text-align: center;
			width: 65px;
			height: 65px;
			font-size: 14px;
			line-height: 1.1;
			.day {
				display: block;
				font-size: 26px;
				font-weight: 700;
			}
		}
	}

	.rt-post-meta  {
		margin-bottom: 12px;
	}

}

.blog-grid-2.is-above-meta {
	.post-thumbnail {
		@media(max-width: 1199px) {
			clip-path: none;
		}
	}
}

.blog-grid-3 {
	.post-thumbnail {
		border-radius: 15px;
	}
	.article-inner-wrapper {
		position: relative;
		overflow: hidden;
		border-radius: 15px;
		&:before {
			content: '';
			position: absolute;
			background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.90) 100%);
			left: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			z-index: 1;
		}
	}
	.entry-wrapper {
		position: absolute;
		left: 0;
		bottom: 0;
		padding: 0 30px 26px;
		color: $white_color;
		z-index: 1;
		.entry-title {
			a {
				color: $white_color;
				&:hover {
					color: $tertiary_color;
				}
			}
		}
	}
	.rt-post-meta {
		color: $white_color;
		a {
			color: $white_color;
			&:hover {
				color: $tertiary_color;
			}
		}
	}
}
.blog-grid-4 {
	.post-thumbnail, img {
		border-radius: 15px;
	}
	.separate-meta {
		a {
			background-color: $gray20;
			border: 1px solid $border_color;
			color: $primary_color;
			border-radius: 50px;
			&:hover {
				border: 1px solid $primary_color;
				color: $primary_color;
			}
		}
	}
	.is-image {
		.post-thumbnail-wrap {
			margin-bottom: 34px;
		}
		.separate-meta {
			position: absolute;
			left: 24px;
			bottom: -24px;
			z-index: 1;
		}
	}

	&.no-above-meta {
		.entry-wrapper {
			margin-top: 20px;
		}
	}
}
.sticky {
	.article-inner-wrapper {
		background-color: rgba($primary_rgb, 0.08);
		border-color: $primary_color;
		border-radius: 15px;
	}
}
.blog-grid-2.sticky {
	.entry-wrapper {
		padding: 0 24px 24px;
	}
}

.blog-grid-4.sticky {
	.entry-wrapper {
		padding: 0 24px 24px;
	}
}
